/* eslint-disable camelcase */
import PropTypes from 'prop-types';
import { Page, View, Text, Image, Document } from '@react-pdf/renderer';
//
import styles from './InvioceStyle';
import { APP_NAME } from '../../../../config';


// ----------------------------------------------------------------------

InvoicePDF.propTypes = {
  invoice: PropTypes.object.isRequired,
  MapData: PropTypes.any.isRequired
};

export default function InvoicePDF({ invoice, MapData }) {
  // eslint-disable-next-line camelcase
  const {  network, card_name, plan_name, load_pin} = invoice;


  return (
    <Document>
      <Page size="A4" style={styles.page}>
        <Text style={[styles.overline, styles.mb8]}> Recharge Card Printing  Details By {APP_NAME}</Text> 

        <View style={styles.row}>
        {MapData.map((card, index) => (  
            <View style={styles.col}>
              <View style={styles.card}>
              <View style={styles.toplogo}>
              <Image source={network === 'MTN' && '/upload/mtn.png' || network === 'AIRTEL' && '/upload/airtel.png' || network === 'GLO' && '/upload/glo.png' || network === '9MOBILE' && '/upload/mobile.png'} style={{ height: 10 }} />
              <Text style={styles.h4}>{card_name}</Text>
              <Text style={styles.h3}>{plan_name}</Text>
              </View >
              <View style={styles.price}>
                <Text>{`${network} Recharge Card`}</Text>
                </View>
                <Text style={styles.text}>Get {network} Recharge instantly anywhere anytime</Text>
                 <View style={styles.pin}><Text style={{"font-weight" : "bolder", "font-size":"18px" }}> PIN: {card?.pin} </Text>
                 <Text style={styles.pinSpan}>Serial : {card?.serial}</Text>
                 </View>
                 <Text style={styles.text}> Dial {load_pin}{card?.pin}#  to Load</Text>
               </View>
               </View>
        ))}
        </View>

          </Page>
    </Document>
  );
}
